var _this;
var timer = null; 
export default {
	data() {
		return {
			banner: [],
			course: [],
 
			page: 1,
			baseurl: "http://39.105.48.243:8080/crlink/"
		} 
	},



	onLoad() {
		_this = this
		this.getAdImage()
		this.getIndexCourse(this.page)

	},
	onShow() {
		this.getuserInfo()
	},

	onPullDownRefresh: function() {
		this.page = 1;
		this.getIndexCourse(this.page)

	},

	onReachBottom: function() { //当划到最底部的时候触发事件
		if (timer != null) { //加载缓冲延迟
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_this.getIndexCourse(_this.page); 
		}, 600);
	},
	methods: {
 
		getuserInfo() {
			// 
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('user!ajaxGetUserInfo.action', {
				loginKey: loginkey
			}).then(res => {
				if (res.res.status == 0) {

				} else {
					uni.removeStorageSync('loginKey');
					uni.removeStorageSync('userId');
				}
			})
		},
		getAdImage() {
			this.$api.post('index!ajaxGetAdImage.action').then(res => {
				if (res.res.status == 0) {
					this.banner = res.inf.arr
				}
			})
		},
		getIndexCourse(e) {
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('index!ajaxGetIndexCollege.action', {
				firstIndex: e,
				loginKey: loginkey
			}).then(res => {
				if (res.res.status == 0) {


					if (e == 1) {
						this.course = res.inf.arr
						_this.page++;
					} else {
						if (e <= res.inf.pageCount) {
							_this.course = _this.course.concat(res.inf.arr); //进行数据的累加
							_this.page++; //页数的++
							_this.loading = "加载更多";
						} else {
							uni.showToast({
								title: '没有更多了！'
							});
						}
					} 
				}
				uni.hideNavigationBarLoading();
				uni.stopPullDownRefresh(); //数据加载完成,刷新结束
			})
		},

		cousersClick() {
			var loginkey = uni.getStorageSync('loginKey');
			if (loginkey.length > 0) {
				uni.navigateTo({
					url: './cousers/cousers',
					success() {

					}
				})
			} else {
				uni.navigateTo({
					url: '../mine/login',    
					success() {

					}
				}) 
			}
		},

		itemClcik(item) { 

			var loginkey = uni.getStorageSync('loginKey');
			if (loginkey.length > 0) {
				uni.navigateTo({
					url: './college?collegeId=' + item.id,
					success() {

					}
				})
			} else {
				uni.navigateTo({
					url: '../mine/login',
					success() {

					} 
				})
			}


		},


		vertSearch() {
			uni.navigateTo({
				url: './certSeach'
			})
		}
	}
}
